let pagina = 1;

document.addEventListener('DOMContentLoaded', function() {
	iniciarApp();
});

function iniciarApp() {
	mostrarServicios();

	// resaltar el tab seleccionado
	mostrarSeccion();

	// mostrar u ocultar seccion de acuerdo al tab seleccionado
	seleccionarSeccion();
}

function mostrarSeccion() {
	const seccionActual = document.querySelector(`#seccion-${pagina}`);
	seccionActual.classList.add('mostrar-seccion');

	// resaltar tab actual
	const tab = document.querySelector(`[data-seccion="${pagina}"]`);
	tab.classList.add('actual');
}

function seleccionarSeccion() {
	const enlaces = document.querySelectorAll('.tabs button');

	enlaces.forEach( enlace => {
		enlace.addEventListener('click', ev => {
			ev.preventDefault();

			pagina = parseInt(ev.target.dataset.seccion);

			// elimina la clase '.mostrar-seccion' de la seccion anterior
			document.querySelector('.mostrar-seccion').classList.remove('mostrar-seccion');

			const seccion = document.querySelector(`#seccion-${pagina}`);
			seccion.classList.add('mostrar-seccion');

			// eliminar la clase actual en el tab anterior
			document.querySelectorAll('.actual').classList.remove('actual');

			// agregar la clase actual al tab seleccionado
			const tab = document.querySelector(`[data-seccion="${pagina}"]`);
			tab.classList.add('actual');
		})
	})
}

async function mostrarServicios() {
	try {
		const resultado = await fetch('./servicios.json');
		const db = await resultado.json();

		const { servicios } = db;

		servicios.forEach(servicio => {
			const { id, nombre, precio } = servicio;

				//  DOM Scripting
			const nombreServicio = document.createElement('P');
			nombreServicio.textContent = nombre;
			nombreServicio.classList.add('nombre-servicio');

			const precioServicio = document.createElement('P');
			precioServicio.textContent = `$ ${precio}`;
			precioServicio.classList.add('precio-servicio');

			const servicioDiv = document.createElement('DIV');
			servicioDiv.classList.add('servicio');
			servicioDiv.dataset.idServicio = id;

			servicioDiv.onclick = seleccionarServicio;

			// agregar nombre y precio al div de servicio
			servicioDiv.appendChild(nombreServicio);
			servicioDiv.appendChild(precioServicio);

			// inyectar en el HTML
			document.querySelector('#servicios').appendChild(servicioDiv);
		});

	} catch (error) {
		console.log(error);
	}
}

function seleccionarServicio(ev) {

	let elemento;

	if (ev.target.tagName === 'P') {
		elemento = ev.target.parentElement;
	} else {
		elemento = ev.target;
	}

	if (elemento.classList.contains('seleccionado')) {
		elemento.classList.remove('seleccionado');
	} else {
		elemento.classList.add('seleccionado');
	}
}
